import React from "react";

import { ReactComponent as DollarImg } from './images/icon-dollar.svg';



function BillInput(props){

    function getBillInput(event){
      props.billInputCatch(parseFloat(parseFloat(event.target.value).toFixed(2)))
    }
  
    return (
    <div className="param-input-container">
        <h2>Bill</h2>
        <input 
        type="number" 
        className="bill-input bill parameter parameter--bill" 
        name="bill-input" 
        placeholder="0" 
        required 
        onInput={getBillInput} 
        value={props.bill}/>
        <div className="img">
        <DollarImg />
        </div>
        
      </div>
    );
  }

export default BillInput;