
import React from "react";
import { ReactComponent as PplImg } from './images/icon-person.svg';


function PplInput(props){

    function getNbPpl(event){
      props.pplInputCatch(parseInt(event.target.value));
    }

    function setRequired(){
      props.requiredCatch(true)
      // event.target.required = true;
    }
  
    return(
      <div className="param-input-container">
        <h3>Number of People</h3>
        
        <input 
        type="number" 
        className="parameter parameter--ppl"
        min="1" 
        placeholder="0"
        required={props.required}
        onInput={getNbPpl} 
        onBlur={setRequired}
        value={props.ppl}/>
        <p className="error">Can't be zero</p>
        <div className="img">
        <PplImg />
        </div>
      </div>
    )
    
  }

  export default PplInput;