import React from "react";

import Parameters from "./Parameters";
import Results from "./Results";
import Header from "./Header";
import Footer from "./Footer";
import { useState } from "react";


function App() {

  const[bill, setBill] = useState(0.00);
  const[tip, setTip] = useState(0.00);
  const[ppl, setPpl] = useState(0.00);
  let splittedTip = 0.00;
  let splittedAmount = 0.00;
  const[params,setParams] = useState({
    paramBill:"",
    paramCustomTip:"",
    paramPpl:"",
    paramRequired:""
  });

    
  const inputs = [bill, tip, ppl];
  let results = [splittedTip, splittedAmount];


// BILL SETTING 
  function billSetting(newInput){
    setBill(newInput);
    setParams({
      billParam:newInput
    });
  }



  // TIP SETTING 
  function tipSetting(newInput1,newInput2){
    setTip(newInput1);
    console.log("boule"+newInput2);
    setParams({
      paramTip:true
    });
  }


  const percentTips = document.querySelectorAll(".percent-tip");

  function customSetting(newInput){
    console.log("hello");
    setTip(newInput/100);
    setParams({
      paramCustomTip:newInput
    });

    percentTips.forEach((percentTip)=>{
      console.log("hi"+percentTip);
      percentTip.checked = false;
    });
  }

  // PEOPLE SETTING　
  function pplSetting(newInput){
    setPpl(newInput);
    setParams({
      pplParam:newInput
    });
  }

  function requiredSetting(newInput){
    setParams({
      paramRequired:newInput
    })
  }

  // RESET　BUTTON 
  function resetToZero(input){
    // reset to 0 every parameters for the calculation
    const [resetBill, resetTip, resetPpl] = input;
    setBill(resetBill);
    setTip(resetTip);
    setPpl(resetPpl);
    // reset parameters to their original state 
  }

  function resetParams(){
    setParams({
      paramBill:"",
      paramCustomTip:"",
      paramPpl:"",
      paramRequired:""
    });
  }



  function resetInput(newInput){
    resetToZero(newInput);
    resetParams();
    percentTips.forEach((percentTip)=>{
      console.log("hi"+percentTip);
      percentTip.checked = false;
    });
  }

  if(ppl !== 0){
    splittedTip = parseFloat(( bill * tip ) / ppl).toFixed(2);
    splittedAmount = parseFloat(( (bill * tip) + bill ) / ppl).toFixed(2);
    results = [splittedTip, splittedAmount];
    console.log(results);
  }
  
  return (
    <div className="App">
    <Header />
    <main>
    <div className="flex-container">
      <Parameters  
      billCatch={billSetting}
      tipCatch={tipSetting} 
      pplCatch={pplSetting}
      customCatch={customSetting}
      requiredCatch={requiredSetting}
      bill={bill}
      tip={tip}
      ppl={ppl}
      params = {params}
      />
      <Results 
      inputs={inputs}
      results={results}
      resetCatch={resetInput}
      />
    </div>
    </main>

    <Footer /> 
   
    </div>
  );
}

export default App;
