import React from "react";

function Results(props){
  const [resultTip, resultAmount] = props.results;
  let [billInput, tipInput, pplInput ] = props.inputs;

  function handleClick(){
    billInput = 0.00;
    tipInput = 0.00;
    pplInput = 0.00;
    const resetInputs = [billInput, tipInput, pplInput];
    const resetParams = ["",false,""];
    props.resetCatch(resetInputs, resetParams)
  }

    return(
  <section id="results" className="section">
  
  <div className="flex-container--result">
  <div className="flex-container--result__item">
  <h3>Tip Amount</h3>
  <p>/ person</p>
  </div>
  <div className="flex-container--result__item">
  <p className="result-amount tip-amount">${resultTip}</p>
  </div>
  </div>
  
  <div className="flex-container--result">
  <div className="flex-container--result__item">
  <h3>Total</h3>
  <p>/ person</p>
  </div>
  <div className="flex-container--result__item">
  <p className="result-amount total-splitted">${resultAmount}</p>
  </div>
  </div>
  
  <button className="reset-btn" onClick={handleClick}>Reset</button>
  
  </section>
    );
  }

  export default Results;