import React from "react";


function TipInput(props){

    function getTipInput(event){
      props.tipInputCatch(parseFloat(event.target.value));
    }
  
    function getTipCustom(event){
      props.tipCustomInputCatch(parseFloat(event.target.value));
    }
    return(
        <div>
        <h3>Select Tip %</h3>
    <div className="btn-container">

    <label className="tip-button">
      <input type="radio"
       name="percentage-tip" 
       value="0.05" 
       className="percent-tip" 
       onClick={getTipInput}/>
      <span className="checkmark" >5%</span>
    </label>
  
    <label className="tip-button">
      <input type="radio"
       name="percentage-tip"
       value="0.10" 
       className="percent-tip" 
       onClick={getTipInput}/>
      <span className="checkmark">10%</span>
    </label>
   
    <label className="tip-button">
      <input type="radio" 
      name="percentage-tip" 
      value="0.15" 
      className="percent-tip" 
      onClick={getTipInput}/>
      <span className="checkmark">15%</span>
    </label>
  
    <label className="tip-button">
      <input type="radio" 
      name="percentage-tip" 
      value="0.25" 
      className="percent-tip"  
      onClick={getTipInput}/>
      <span className="checkmark">25%</span>
    </label>
  
    <label className="tip-button">
      <input type="radio" 
      name="percentage-tip" 
      value="0.50" 
      className="percent-tip" 
      onClick={getTipInput}/>
      <span className="checkmark">50%</span>
    </label>
    <input type="number" 
    className="parameter--custom parameter" 
    placeholder="Custom" 
    onInput={getTipCustom}
    value={props.tipCustom}/>
    </div>
    </div>
    );
  
  }
  

  export default TipInput;