import React from "react";

import TipInput from './TipInput';
import BillInput from './BillInput';
import PplInput from './PplInput';


function Parameters(props){

  function pplInputSettings(newInput){
    props.pplCatch(newInput);
    
  }
  function billInputSettings(newInput){
    props.billCatch(newInput); 
  }

  function tipInputSettings(newInput){
    props.tipCatch(newInput);    
  }
  function customInputSettings(newInput){
    props.customCatch(newInput);
  }

  function requiredSettings(newInput){
    props.requiredCatch(newInput);
  }

    return(

  <section id="parameters" className="section">
     
<BillInput 
  billInputCatch={billInputSettings}
  bill={props.params.paramBill}
/>
  <TipInput 
    tipInputCatch={tipInputSettings}
    tipCustomInputCatch={customInputSettings}
    tipCustom={props.params.paramCustomTip}
  />
  <PplInput 
  pplInputCatch={pplInputSettings}
  requiredCatch={requiredSettings}
  ppl={props.params.paramPpl}
  required={props.params.paramRequired}

   />
  
  </section>
    );
  }

  export default Parameters;
  
